





















import { Component, Vue } from 'vue-property-decorator';
import CertShow from '@/componets/cert_show.vue';
import { Divider } from 'vant';
import {Cert, CertOwn, StatusChain} from '@/entry/diamond';

Vue.use(Divider);

@Component({
    components: {CertShow},
})
export default class Process extends Vue {
    private showInfoConfirm = false;
    private showSigned = false;
    private showSended = false;
    private showfinished = false;
    private certOwn!: CertOwn;
    public created(): void {
        this.$store.state.title = this.$t('cert.process');
        // todo 注册状态变化,  这里特别注意缓存的问题， 进入这个页面，是否一定会有created？
        if (!this.certOwn) {
            this.certOwn = this.$store.state.data.certOwn;
        }

        if (this.certOwn) {
            const diamond = this.$store.state.data.diamond;
            diamond.registerCert(this.certOwn, (it: CertOwn) => {
                switch (it.cert.status) {
                    case StatusChain.Confirm:
                        break;
                    case StatusChain.Error:
                        break;
                    case StatusChain.Pendding:
                        break;
                    case StatusChain.Sended:
                        break;
                }
            });

            if (this.certOwn.hasOwn()) {
                diamond.registerOwn(this.certOwn, (it: CertOwn) => {
                    switch (it.own.status) {
                        case StatusChain.Confirm:
                            break;
                        case StatusChain.Error:
                            break;
                        case StatusChain.None:
                            break;
                        case StatusChain.Pendding:
                            break;
                        case StatusChain.Sended:
                            break;
                    }
                });
            }
        }
    }

    public beforeDestroy(): void {
        if (this.certOwn) {
            const diamond = this.$store.state.data.diamond;
            diamond.unRegisterCert(this.certOwn);
            if (this.certOwn.hasOwn()) {
                diamond.unRegisterOwn(this.certOwn);
            }
        }
    }
}
